<template>
    <div class="shopcart">
        
        <div v-if="shopcartData.length > 0">

            <van-nav-bar
                title="购物车"
                :right-text="isManage ? '管理' : '完成'"
                @click-right="isManage = !isManage"
            />
            
            <!-- 商品列表 -->
            <div class="list">
                <!-- <van-pull-refresh
                    v-model="refreshing"
                    success-text="刷新成功"
                    @refresh="onRefresh"
                > -->
                    <!-- <van-list
                        v-model="isLoading"
                        :finished="finished"
                        :offset="50"
                        finished-text="没有更多了"
                        @load="onLoad"
                    > -->
                        <van-swipe-cell v-for="(item, index) in shopcartData" :key="index">
                            <div class="list-item">
                                <!-- 复选框 -->
                                <van-checkbox v-model="item.isChecked" @click="singleSelection"></van-checkbox>
                                <!-- 商品信息 -->
                                <div class="goods-info">
                                    <div class="goods-img">
                                        <img class="auto-img" :src="item.small_img" alt="">
                                    </div>
                                    <div class="goods-content">
                                        <div class="goods-text">
                                            <div class="goods-name">
                                                <span class="goods-title">{{ item.name }}</span>
                                                <span class="goods-rule">{{ item.rule }}</span>
                                            </div>
                                            <div class="goods-enname">{{ item.enname }}</div>
                                        </div>

                                        <div class="price">
                                            <div class="goods-price">￥{{ item.price }}</div>
                                            <div class="goods-count">
                                                <van-stepper v-model="item.count" theme="round" button-size="20" disable-input @change="editCount(item)" />
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </div>
                            <template #right>
                                <van-button square text="删除" type="danger" class="delete-button" @click="removeGoods([item.sid], index)" />
                            </template>
                        </van-swipe-cell>
                    <!-- </van-list> -->

                <!-- </van-pull-refresh> -->
            </div>

        </div>
        <!-- 如果购物车没有商品, 则显示空页面 -->
        <div v-else>
            <van-empty description="空空如也, 快去逛一逛吧" />
            <p class="txt"><span @click="$router.push({ name: 'Home' })">去购物 &gt;</span></p>
        </div>

        <template v-if="isManage">
            <van-submit-bar :price="total" :disabled="isDisable" button-text="提交订单" @submit="submitOrder">
                <van-checkbox v-model="checkedAll" @click="selectAll">全选</van-checkbox>
            </van-submit-bar>
        </template>
        <template v-else>
            <van-submit-bar :disabled="isDisable" button-text="删除选择" @submit="removeSelect">
                <van-checkbox v-model="checkedAll" @click="selectAll">全选</van-checkbox>
            </van-submit-bar>
        </template>

        <NavBar></NavBar>
    </div>
</template>

<script>
    import { Toast } from "vant";
    // 导入导航组件
    import NavBar from "@/components/NavBar";

    export default {
        name: "Shopcart",
        data() {
            return {
                // 管理
                isManage: true,
                // 全选
                checkedAll: false,
                // 禁选
                isDisable: true,
                // 购物车数据
                shopcartData: [],
                // 每次加载的数据
                loadData: [],
                // refreshing: false,
                // 滚动加载
                isLoading: true,
                finished: false,
                // 每次加载的个数
                loadCount: 8,
            };
        },

        methods: {
            // 获取购物车数据
            getShopcartData() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findAllShopcart",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 5000) {
                        res.data.result.forEach(ele => {
                            ele.isChecked = false;
                        });

                        this.shopcartData = res.data.result;

                        // this.loadData = this.shopcartData.splice(0, this.loadCount);


                        // if (this.shopcartData.length > 0) {
                        //     this.isLoading = false;
                        // } else {
                        //     this.isLoading = true;
                        //     this.finished = true;
                        // }

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 全选
            selectAll() {
                this.shopcartData.forEach(item => {
                    item.isChecked = this.checkedAll;

                });

                this.isDisable = !this.checkedAll;
            },

            // 单选
            singleSelection() {

                this.isDisable = true;

                for(let i = 0; i < this.shopcartData.length; i++) {

                    if(this.shopcartData[i].isChecked){
                        this.isDisable = !this.shopcartData[i].isChecked;
                        break;
                    }
                }

                for(let i = 0; i < this.shopcartData.length; i++) {

                    if(!this.shopcartData[i].isChecked){
                        this.checkedAll = this.shopcartData[i].isChecked;
                        return;
                    }

                }
                this.checkedAll = true;

            },

            // 修改商品数量
            editCount(item) {

                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/modifyShopcartCount",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        // 商品id
                        sid: item.sid,
                        // 商品数量
                        count: item.count,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();


                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();
    

                });
            },

            // 删除单个商品
            removeGoods(sids, index) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.remove(tokenString, sids, index);

            },

            // 删除选择的商品
            removeSelect() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                let sids = [];
                this.shopcartData.forEach(item => {
                    if(item.isChecked) {
                        sids.push(item.sid);
                    }
                });


                this.remove(tokenString, sids);
            },

            // 删除商品函数
            remove(tokenString, sids, index) {
                this.axios({
                    method: "POST",
                    url: "/removeShopcart",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        sids: JSON.stringify(sids),
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 7000) {

                        if (index == undefined) {

                            for(let i = this.shopcartData.length - 1; i >= 0; i--) {
                                if(this.shopcartData[i].isChecked) {
                                    this.shopcartData.splice(i, 1);
                                };
                            }

                        } else {
                            this.shopcartData.splice(index, 1);
                        }

                        Toast.success("删除成功");
                    } else{
                        Toast.success("删除失败");
                    }
                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();

                });
            },

            // 提交订单
            submitOrder() {
                let sids = [];

                this.shopcartData.forEach(item => {

                    if(item.isChecked) {
                        sids.push(item.sid);
                    }

                });


                this.$router.push({ 
                    name: "Pay",
                    query: {
                        sids: sids.join("-"),
                    },
                });

            },

            // 下拉刷新
            // onRefresh() {
                
            //     this.finished = false;

            //     this.isLoading = true;

            //     this.onLoad();
            // },

            // 懒加载
            // onLoad() {
            //     setTimeout(() => {
            //         this.loadData.push(...this.shopcartData.splice(0, this.loadCount));

            //         if(this.shopcartData.length == 0) {
            //             this.isLoading = true;
            //             this.finished = true;
            //         }

            //     }, 1000);

            // }
        },
        
        components: {
            NavBar,
        },

        computed: {
            // 计算价格
            total() {
                let pic = 0;

                this.shopcartData.forEach(item => {
                    if(item.isChecked) {
                        pic += item.count * item.price * 100;
                    }
                });

                return pic;
            },
        },

        created() {
            // 获取购物车数据
            this.getShopcartData();

            this.$store.state.active = 2;

        },
    }
</script>

<style lang="less" scoped>
    .shopcart {
        color: #646566;
        
        .van-nav-bar{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 45px;
        // 
            box-shadow: 0 -4px 10px 0px #f8f8f8;
        }
    }

    .txt {
        text-align: center;

        span {
            color: #1989FA;
            text-decoration: underline;
        }
    }

    .list{
        height: calc(100vh - 45px * 3);
        margin-top: 45px;
        // overflow: hidden;
        overflow-y: auto;

        .list-item{
            display: flex;
            margin-top: 1px;
            padding: 10px;
            background-color: #fff;

            .van-checkbox{
                width: 30px;
            }

            .goods-info{
                display: flex;
                flex: 1;
                
                .goods-img{
                    width: 70px;
                    height: 70px;
                    background-color: #1989FA;
                }

                .goods-content{
                    display: flex;
                    flex: 1;
                    margin-left: 10px;
                    flex-direction: column;
                    justify-content: space-between;

                    .goods-text{

                        .goods-title{
                            font-size: 16px;
                        }

                        .goods-rule{
                            margin-left: 5px;
                            color: #c4c4c4;
                        }

                        .goods-enname{
                            color: #8b8b8b;
                            font-size: 13px;
                            line-height: 20px;
                        }
                    }

                    .price{
                        display: flex;
                        justify-content: space-between;

                        .goods-price{
                            color: #1989FA;
                            font-size: 16px;
                        }

                        /deep/ .van-stepper__minus{
                            color: #1989FA;
                            border-color: #1989FA;
                        }

                        /deep/ .van-stepper__plus{
                            background-color: #1989FA;
                        }
                    }
                }
            }
        }

        /deep/ .van-button {
            height: 100%;
        }

    }

    .van-submit-bar{
        bottom: 45px;
        
        .van-submit-bar__bar{
            height: 45px;
            justify-content: space-between;
        }

        .van-submit-bar__price{
            color: #1989FA;
        }

        .van-submit-bar__button{
            height: 35px;
        }

        .van-submit-bar__button--danger{
            background: linear-gradient(to right, #6fb7ff, #1989FA);
        }

    }
</style>